import React from 'react';
import { Link } from 'react-router-dom';
import classnames from 'classnames';
import { subscribeEvent, unsubscribeEvent, RESIZE_TYPE } from 'helper/domEventUtil';
import { NAV } from 'router/config';
import Style from './MobileWorkListContainer.scss';

class MobileWorkListContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    // create a ref to store the textInput DOM element
    this.containerRef = React.createRef();
    this.imageHeightList = [0];
    this.hJumpToCurrent = null;
  }

  componentDidUpdate(prevProps) {
    const { workId, category } = this.props;
    this.jumpToCurrentWork();
    // goto the detail page
    if (prevProps.workId !== workId) {
      document.location.href = `#/${category}/${workId}/detail`;
    }
  }

  onImageLoad(e, index) {
    const width = e.currentTarget.offsetWidth;
    const height = e.currentTarget.offsetHeight;
    e.currentTarget.parentElement.style.width = `${width}px`;
    e.currentTarget.parentElement.style.height = `${height}px`;
    e.currentTarget.parentElement.style.display = 'block';
    // need to reset width/height first
    const rect = e.currentTarget.parentElement.getBoundingClientRect();
    //
    this.imageHeightList[index + 1] = rect.height;
    clearTimeout(this.hJumpToCurrent);
    this.hJumpToCurrent = setTimeout(() => this.jumpToCurrentWork(), 200);
  }

  jumpToCurrentWork() {
    const index = this.props.workId;
    let y = 0;
    for (let i = 0; i <= index; i++) {
      y += this.imageHeightList[i];
    }
    // https://stackoverflow.com/questions/35266606/window-scrollto-not-working-in-ios-chrome
    window.scroll({
      top: y
      // fix not working on real mobile device
      // behavior: 'smooth'
    });
  }

  getCurrentLink(index) {
    const { workId, category } = this.props;
    if (workId === index) {
      return `/${category}/${index}/detail`;
    } else {
      return `/${category}/${index}`;
    }
  }

  render() {
    const { workList } = this.props;
    // const currentWork = workList[workId];
    const workListView = workList.map((work, index) => (
      <Link to={this.getCurrentLink(index)} key={work.thumb}>
        <img onLoad={e => this.onImageLoad(e, index)} src={work.thumb} className="img-fluid" />
      </Link>
    ));
    return (
      <section ref={this.containerRef} className="mobile-work-list-container">
        {workListView}
      </section>
    );
  }
}

export default MobileWorkListContainer;
