import { connect } from 'react-redux';
import React from 'react';
import is from 'is_js';
import { withRouter } from 'react-router-dom';
import { getRoute } from 'router/util';
import { NAV } from 'router/config';
import { loadInteractiveWorkAction } from 'action/interactiveWorkAction';
import { loadCreativeWorkAction } from 'action/creativeWorkAction';
import MegaMenuContainer from 'view/component/megamenu/MegaMenuContainer';
import { selectWorkList as selectInteractiveWorkList } from 'model/reducer/interactiveWorkReducer';
import { selectWorkList as selectCreativeWorkList } from 'model/reducer/creativeWorkReducer';
import MediaListContainer from 'view/component/workdetail/MediaListContainer';
import PasswordBox from 'view/component/workdetail/PasswordBox';
import { ga } from 'helper/analysisUtil';
import Style from './WorkDetailPageContainer.scss';

const getCurrentWork = (state, url) => {
  const currentCategory = getRoute(url);
  const currentId = getRoute(url, 1);
  const selector = currentCategory === NAV.PAGE_INTERACTIVE.key ? selectInteractiveWorkList : selectCreativeWorkList;
  const currntWorkList = selector(state);
  return currntWorkList[parseInt(currentId)];
};

class WorkDetailPageContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    if (this.props.currentCategory === NAV.PAGE_INTERACTIVE.key) {
      this.props.loadInteractiveWork();
    } else {
      this.props.loadCreativeWork();
    }
    this.workContentRef = React.createRef();
    this.readMoreRef = React.createRef();
    this.state = {
      showContent: false
    };
  }

  componentDidMount() {
    // eg: #/interactive/0/detail
    ga.track(this.props.match.url);
    window.scrollTo(0, 0);
    this.updateWorkContentHeight();
  }

  onReadMoreClick() {
    this.workContentRef.current.removeAttribute('style');
    this.readMoreRef.current.style.display = 'none';
  }

  updateWorkContentHeight() {
    if (is.mobile() && this.workContentRef.current) {
      // default height
      this.workContentRef.current.style.height = '20em';
    }
  }

  onEnterPassword() {
    this.setState({ showContent: true }, this.updateWorkContentHeight);
  }

  render() {
    const { name, subtitle, date, text, primaryColor, secondaryColor, bgColor, media, password } =
      this.props.currentWork || {};
    // console.log('this.props.currentWork', this.props.currentWork);
    const readMoreStyle = { backgroundImage: `linear-gradient(to bottom, transparent, ${bgColor} 20%)` };
    const shouldShowWork = !password || this.state.showContent;
    const shouldShowPasswordBox = !shouldShowWork;
    return [
      <MegaMenuContainer key="MegaMenuContainer" path={this.props.match.path} />,
      <section
        key="work-detail-page-container"
        style={{ backgroundColor: bgColor }}
        className="work-detail-page-container"
      >
        {shouldShowPasswordBox && <PasswordBox password={password} callback={() => this.onEnterPassword()} />}
        {shouldShowWork && (
          <div className="head-container">
            {name && <h1 style={{ color: primaryColor }}>{name}</h1>}
            {subtitle && <h5 style={{ color: secondaryColor }}>{subtitle}</h5>}
            {date && <h6 style={{ color: secondaryColor }}>{date}</h6>}
            <div ref={this.workContentRef} className="work-content">
              {text && <div style={{ color: primaryColor }} dangerouslySetInnerHTML={{ __html: text }} />}
              <p className="read-more" ref={this.readMoreRef} style={readMoreStyle}>
                <button style={{ color: primaryColor }} onClick={() => this.onReadMoreClick()}>
                  Read More
                </button>
              </p>
            </div>
          </div>
        )}
        {shouldShowWork && media && <MediaListContainer media={media} />}
      </section>
    ];
  }
}

const mapStateToProps = (state, ownProps) => ({
  currentCategory: getRoute(ownProps.match.url),
  currentWork: getCurrentWork(state, ownProps.match.url)
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  loadInteractiveWork: () => dispatch(loadInteractiveWorkAction()),
  loadCreativeWork: () => dispatch(loadCreativeWorkAction())
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(WorkDetailPageContainer));
