import React from 'react';
import _ from 'lodash';
import classnames from 'classnames';
import { subscribeEvent, unsubscribeEvent, RESIZE_TYPE } from 'helper/domEventUtil';
import { NAV } from 'router/config';
import previousButtonIcon from './previous-home.isvg';
import nextButtonIcon from './next-home.isvg';
import Style from './ThumbSlide.scss';

class ThumbSlide extends React.Component {
  constructor(props) {
    super(props);
    // create a ref to store the textInput DOM element
    this.slideRef = React.createRef();
    this.contentRef = React.createRef();
    this.nextRoute = null;
  }

  componentDidMount() {
    this.nextRoute = this.props.nextRoute;
  }

  shouldComponentUpdate(nextProps, nextState) {
    this.nextRoute = nextProps.nextRoute;
    this.updateContentSize(nextProps.width, nextProps.height);
    this.gotoCurrentImage(nextProps.height, nextProps.workId);
    return false;
  }

  // TODO: nextRoute
  getImageList(images, width, height) {
    return images.map(work => (
      <button style={{ width: `${width}px`, height: `${height}px` }} key={`key-${work.thumb}`}>
        <img src={work.thumb} />
      </button>
    ));
  }

  updateContentSize(width, height) {
    // update container
    this.slideRef.current.style.width = `${width}px`;
    this.slideRef.current.style.height = `${height}px`;
    //
    const images = this.contentRef.current.children;
    // NOTICE: images is not array but object
    _.each(images, node => {
      node.style.width = `${width}px`;
      node.style.height = `${height}px`;
    });
  }

  gotoCurrentImage(height, index) {
    const el = this.contentRef.current;
    const top = height * index;
    el.style.top = `-${top}px`;
    // console.log('el.style.top', el.style.top);
  }

  onThumbClick(e) {
    document.location.href = `#${this.nextRoute}`;
  }

  render() {
    const { workId, workList, width, height } = this.props;
    const top = height * workId;
    return (
      <div
        ref={this.slideRef}
        onClick={e => this.onThumbClick(e)}
        className="thumb-slide"
        style={{ width: `${width}px`, height: `${height}px` }}
      >
        <div ref={this.contentRef} className="slide-content" style={{ top: `-${top}px` }}>
          {this.getImageList(workList, width, height)}
        </div>
      </div>
    );
  }
}

export default ThumbSlide;
