import { connect } from 'react-redux';
import React from 'react';
// https://stackoverflow.com/questions/53539314/what-is-withrouter-for-in-react-router-dom
import { withRouter } from 'react-router-dom';
import MegaMenuContainer from 'view/component/megamenu/MegaMenuContainer';
import { loadAboutMeWorkAction } from 'action/aboutMeAction';
import { selectAboutMe, selectHeadImage } from 'model/reducer/aboutMeReducer';
import { ga } from 'helper/analysisUtil';
import Style from './AboutMePageContainer.scss';

class AboutMePageContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    this.props.loadAboutMe();
  }

  componentDidMount() {
    ga.track('/about');
  }

  render() {
    return [
      <MegaMenuContainer key="MegaMenuContainer" path={this.props.match.path} />,
      <section key="about-me-page-container" className="about-me-page-container">
        <div className="head-container">
          <img className="img-fluid" src={this.props.headImage} />
        </div>
        <div className="about-me-content" dangerouslySetInnerHTML={{ __html: this.props.aboutMe }} />
      </section>
    ];
  }
}

const mapStateToProps = (state, ownProps) => ({
  aboutMe: selectAboutMe(state),
  headImage: selectHeadImage(state)
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  loadAboutMe: () => dispatch(loadAboutMeWorkAction())
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AboutMePageContainer));
