import { combineReducers } from 'redux';
import interactiveWorkReducer, { path as interactiveWorkReducerPath } from 'model/reducer/interactiveWorkReducer';
import creativeWorkReducer, { path as creativeWorkReducerPath } from 'model/reducer/creativeWorkReducer';
import aboutMeReducer, { path as aboutMeReducerPath } from 'model/reducer/aboutMeReducer';

export default combineReducers({
  [interactiveWorkReducerPath]: interactiveWorkReducer,
  [creativeWorkReducerPath]: creativeWorkReducer,
  [aboutMeReducerPath]: aboutMeReducer
});
