import React from 'react';
import _ from 'lodash';
import classnames from 'classnames';
import { subscribeEvent, unsubscribeEvent, RESIZE_TYPE } from 'helper/domEventUtil';
import { NAV } from 'router/config';
import previousButtonIcon from './previous-home.isvg';
import nextButtonIcon from './next-home.isvg';
import Style from './NumberSlide.scss';

const TEXT_HEIGHT = 180;

export const addPaddingZero = num => {
  if (num > 9) {
    return String(num);
  } else {
    return `0${num}`;
  }
};

class NumberSlide extends React.Component {
  constructor(props) {
    super(props);
    // create a ref to store the textInput DOM element
    this.contentRef = React.createRef();
  }

  shouldComponentUpdate(nextProps, nextState) {
    this.gotoCurrentNumber(nextProps.workId);
    return false;
  }

  getNumberList(totalWorks) {
    return _.times(totalWorks, index => <h2 key={`key-number-${index}`}>{addPaddingZero(index + 1)}</h2>);
  }

  gotoCurrentNumber(index) {
    const el = this.contentRef.current;
    const top = TEXT_HEIGHT * index;
    el.style.top = `-${top}px`;
  }

  render() {
    const { workId, totalWorks } = this.props;
    const top = TEXT_HEIGHT * workId;
    return (
      <div className="number-slide">
        <div ref={this.contentRef} className="slide-content" style={{ top: `-${top}px` }}>
          {this.getNumberList(totalWorks)}
        </div>
      </div>
    );
  }
}

export default NumberSlide;
