import React from 'react';
import { Link } from 'react-router-dom';
import classnames from 'classnames';
import { subscribeEvent, unsubscribeEvent, RESIZE_TYPE } from 'helper/domEventUtil';
import { NAV } from 'router/config';
import { ga } from 'helper/analysisUtil';
import NumberSlide, { addPaddingZero } from './NumberSlide';
import ThumbSlide from './ThumbSlide';
import previousButtonIcon from './previous-home.isvg';
import nextButtonIcon from './next-home.isvg';
import Style from './WorkFrameContainer.scss';

// const ratio = 670 425
const ORIGINAL_WIDTH = 670;
const ORIGINAL_HEIGHT = 425;
const MARGIN_BOTTOM = 140;

const getDecoratingImagesView = category => {
  const map = {};
  //
  map[NAV.PAGE_INTERACTIVE.key] = [
    <div key="interactive-top-image" className="interactive-top-image" />,
    <div key="interactive-right-image" className="interactive-right-image" />
  ];
  map[NAV.PAGE_CREATIVE.key] = [
    <div key="creative-left-top-image" className="creative-left-top-image" />,
    <div key="creative-left-bottom-image" className="creative-left-bottom-image" />,
    <div key="creative-right-top-image" className="creative-right-top-image" />,
    <div key="creative-right-bottom-image" className="creative-right-bottom-image" />
  ];
  return map[category];
};

class WorkFrameContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    // create a ref to store the textInput DOM element
    this.workFrameRef = React.createRef();
    this.state = {
      width: 0,
      height: 0
    };
  }

  componentDidMount() {
    subscribeEvent(RESIZE_TYPE, this.onWindowResize, this);
    this.onWindowResize();
  }

  componentWillUnmount() {
    unsubscribeEvent(RESIZE_TYPE, this.onWindowResize);
  }

  componentDidUpdate(prevProps, prevState) {
    // eg: #/interactive/0
    ga.track(this.props.currentRoute);
  }

  onWindowResize() {
    // console.log('onWindowResize args', args, a, b, c, d);
    // https://stackoverflow.com/questions/6942785/window-innerwidth-vs-document-documentelement-clientwidth
    // window.innerHeight
    // document.documentElement.clientHeight
    const clientWidth = window.innerWidth;
    const clientHeight = window.innerHeight;
    //
    const el = this.workFrameRef.current;
    //
    const height = clientHeight - el.offsetTop - MARGIN_BOTTOM;
    const width = (ORIGINAL_WIDTH * height) / ORIGINAL_HEIGHT;
    // NOTICE: the reason to directly add width/height is to make sure
    // the container doesn't get jump when the first change
    el.style.width = width + 'px';
    el.style.height = height + 'px';
    //
    this.setState({ width, height });
  }

  render() {
    const { workId, workList, category, currentRoute } = this.props;
    const totalWorks = workList.length;
    const work = workList[workId];
    return (
      <section ref={this.workFrameRef} className="work-frame-container">
        {this.state.width && this.state.height && (
          <ThumbSlide
            workId={workId}
            workList={workList}
            nextRoute={`${currentRoute}/detail`}
            width={this.state.width}
            height={this.state.height}
          />
        )}
        <NumberSlide workId={workId} totalWorks={totalWorks} />
        <h3>{`/${addPaddingZero(totalWorks)}`}</h3>
        <h5>{work.name}</h5>
        <h6>{work.date}</h6>
        <Link
          className={classnames('button-previous', { 'button-disabled': workId === 0 })}
          to={`/${category}/${workId - 1}`}
          dangerouslySetInnerHTML={{ __html: previousButtonIcon }}
        />
        <Link
          className={classnames('button-next', { 'button-disabled': workId === totalWorks - 1 })}
          to={`/${category}/${workId + 1}`}
          dangerouslySetInnerHTML={{ __html: nextButtonIcon }}
        />
        {getDecoratingImagesView(category)}
      </section>
    );
  }
}

export default WorkFrameContainer;
