import { handleActions } from 'redux-actions';
import * as asyncAction from 'action/creativeWorkAction';

export const path = 'creativeWork';

// === all selectors ===
export const selectWorkList = state => state[path].workList;

export const initialState = { workList: [] };
export default handleActions(
  {
    // NOTICE: if you want to use yield select(selectWorkType), the following must be executed
    // [String(asyncAction.loadWorkAction)] : (state, {payload} = action) => ({...state, ...payload}),
    [String(asyncAction.loadCreativeWorkAction.success)]: (state, { payload }) => ({
      ...state,
      workList: payload
    })
  },
  initialState
);
