import { handleActions } from 'redux-actions';
import * as asyncAction from 'action/aboutMeAction';

export const path = 'aboutMe';

// === all selectors ===
export const selectAboutMe = state => state[path].aboutMe;
export const selectHeadImage = state => state[path].head;

export const initialState = { aboutMe: '' };
export default handleActions(
  {
    [String(asyncAction.loadAboutMeWorkAction.success)]: (state, { payload }) => ({
      ...state,
      ...payload
    })
  },
  initialState
);
