import { connect } from 'react-redux';
import React from 'react';
import { NAV } from 'router/config';
import { ga } from 'helper/analysisUtil';
import classnames from 'classnames';
import Style from './HomePageContainer.scss';

class HomePageContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = { moveRight: false };
  }

  componentDidMount() {
    ga.track('/home');
    setTimeout(() => {
      this.setState({ moveRight: true });
      // NOTICE: set 1.2s in HomePageContainer too
      setTimeout(() => this.goToNextPage(), 1.2 * 1000);
    }, Math.random() * 1000);
  }

  goToNextPage() {
    document.location.href = `#/${NAV.PAGE_INTERACTIVE.key}/0`;
  }

  render() {
    return [
      <section key="about-me-page-container" className="home-page-container">
        <div className={classnames('loading-icon', { 'loading-icon-move-right': this.state.moveRight })} />
      </section>
    ];
  }
}

const mapStateToProps = (state, ownProps) => ({});

const mapDispatchToProps = (dispatch, ownProps) => ({});

export default connect(mapStateToProps, mapDispatchToProps)(HomePageContainer);
