import { takeEvery, put, call } from 'redux-saga/effects';

export const createAsyncWorker = (asyncAction, payloadProcessor, isRequestRequired = true) =>
  function* saga(requestAction) {
    const { payload, meta } = requestAction;
    const shouldRequest = yield call(isRequestRequired, payload, meta);
    if (shouldRequest) {
      try {
        const response = yield call(payloadProcessor, payload);
        yield put(asyncAction.success(response, meta));
      } catch (error) {
        yield put(asyncAction.failure(error, meta));
      } finally {
        // yield put(asyncAction.fulfill(null, meta));
      }
    } else {
      yield put(asyncAction.skipped(null, meta));
    }
  };

export const createWatcher = (actions, worker) =>
  function* asyncWatcher() {
    yield takeEvery(actions, worker);
  };
