import { processDataServiceRequest } from 'model/service/dataService';
import { NAV } from 'router/config';

const URL_PREFIX = 'res/';
const getFullURL = endpoint => `${URL_PREFIX}${endpoint}.json?version=v2.2.0`;

export const requestInteractiveData = payload => processDataServiceRequest(getFullURL(NAV.PAGE_INTERACTIVE.key));
export const requestCreativeData = payload => processDataServiceRequest(getFullURL(NAV.PAGE_CREATIVE.key));
export const requestAboutMeData = payload => processDataServiceRequest(getFullURL(NAV.PAGE_ABOUT.key));
