import _ from 'lodash';
import React from 'react';

export const IMAGE_TYPE = 'image';
export const TEXT_TYPE = 'text';
export const VIDEO_TYPE = 'video';

const countMap = {};
const videoList = [];
let callback = () => {};

export const removeMediaList = () => {
  countMap[IMAGE_TYPE] = 0;
  countMap[TEXT_TYPE] = 0;
  countMap[VIDEO_TYPE] = 0;
  // reset array
  videoList.length = 0;
};

// init
removeMediaList();

const onImageLoad = event => {
  countMap[IMAGE_TYPE]--;
  event.target.classList.add('img-loaded');
  if (countMap[IMAGE_TYPE] === 0) {
    callback(IMAGE_TYPE);
  }
};

const createImage = (src, key = '') => <img className="img-fluid" key={key} onLoad={onImageLoad} src={src} />;

const onTextLoad = event => {
  countMap[TEXT_TYPE]--;
  if (countMap[TEXT_TYPE] === 0) {
    callback(TEXT_TYPE);
  }
};

const createText = (content, key = '') => (
  <p key={key} onLoad={onTextLoad} className="text-center">
    {content}
  </p>
);

const onVideoLoad = event => {
  countMap[VIDEO_TYPE]--;
  videoList.push(event.target);
  if (countMap[VIDEO_TYPE] === 0) {
    callback(VIDEO_TYPE);
  }
};

const createVideo = (url, key = '') => {
  const width = 560;
  const height = 315;
  // console.log('url', url);
  //https://www.youtube.com/embed/6T8stXj7Dzg
  return (
    <iframe width={width} key={key} height={height} onLoad={onVideoLoad} src={url} frameBorder="0" allowFullScreen />
  );
};

export const changeVideoSize = (clientWidth, clientHeight) => {
  videoList.forEach(element => {
    //16 : 9
    let width = clientWidth || 560;
    let height = (width * 9) / 16;
    //start with width and calculate height
    element.width = width;
    element.height = height;
  });
};

export const createMediaList = (media, onLoad) => {
  const list = [];
  callback = onLoad;
  // https://developer.mozilla.org/en-US/docs/Web/HTTP/Basics_of_HTTP/MIME_types
  _.each(media, ({ type, url, content }, index) => {
    const key = `media-${type}-${index}`;
    countMap[type]++;
    switch (type) {
      case TEXT_TYPE:
        list.push(createText(content, key));
        break;
      case VIDEO_TYPE:
        list.push(createVideo(url, key));
        break;
      // set image as default
      case IMAGE_TYPE:
      default:
        list.push(createImage(url, key));
    }
  });
  return list;
};
