import _ from 'lodash';
import React from 'react';
import classnames from 'classnames';
import { Link } from 'react-router-dom';
import { subscribeEvent, unsubscribeEvent, RESIZE_TYPE } from 'helper/domEventUtil';
import Style from './WorkIndicatorContainer.scss';

class WorkIndicatorContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    this.workIndicatorRef = React.createRef();
  }

  componentDidMount() {
    subscribeEvent(RESIZE_TYPE, this.onWindowResize, this);
    this.onWindowResize();
  }

  componentWillUnmount() {
    unsubscribeEvent(RESIZE_TYPE, this.onWindowResize);
  }

  onWindowResize() {
    const clientHeight = window.innerHeight;
    const el = this.workIndicatorRef.current;
    const middleY = (clientHeight - el.offsetHeight) / 2;
    el.style['top'] = `${middleY}px`;
  }

  render() {
    // disable current one to avoid:
    // Warning: Hash history cannot PUSH the same path; a new entry will not be added to the history stack
    const totalIndicators = _.times(this.props.totalWorks, index => (
      <li key={`dot-${index}`}>
        <Link to={`/${this.props.routeKey}/${index}`} className={classnames({ selected: index === this.props.workId })}>
          <span className="indicator-dot" />
        </Link>
      </li>
    ));

    return (
      <section ref={this.workIndicatorRef} className="work-indicator-container">
        <ul>{totalIndicators}</ul>
      </section>
    );
  }
}

export default WorkIndicatorContainer;
