import { connect } from 'react-redux';
import React from 'react';
import is from 'is_js';
import { withRouter } from 'react-router-dom';
import { loadInteractiveWorkAction } from 'action/interactiveWorkAction';
import { selectWorkList } from 'model/reducer/interactiveWorkReducer';
import MegaMenuContainer from 'view/component/megamenu/MegaMenuContainer';
import WorkFrameContainer from 'view/component/workframe/WorkFrameContainer';
import MobileWorkListContainer from 'view/component/mobileworklist/MobileWorkListContainer';
import WorkIndicatorContainer from 'view/component/workindicator/WorkIndicatorContainer';
import { getRoute } from 'router/util';
import { NAV } from 'router/config';
import { ga } from 'helper/analysisUtil';
import Style from './InteractivePageContainer.scss';

class InteractivePageContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    this.props.loadWork();
  }

  componentDidMount() {
    ga.track('/interactive');
  }

  render() {
    const workId = parseInt(this.props.match.params.id);
    const workFrameView = !is.mobile() && this.props.workList[workId] && (
      <WorkFrameContainer
        key="WorkFrameContainer"
        workId={workId}
        workList={this.props.workList}
        currentRoute={this.props.match.url}
        category={NAV.PAGE_INTERACTIVE.key}
      />
    );
    const mobileWorkListView = is.mobile() && this.props.workList[workId] && (
      <MobileWorkListContainer
        key="MobileWorkListContainer"
        workId={workId}
        workList={this.props.workList}
        category={NAV.PAGE_INTERACTIVE.key}
      />
    );
    const workIndicatorView = !is.mobile() && this.props.workList.length > 0 && (
      <WorkIndicatorContainer
        key="WorkIndicatorContainer"
        workId={workId}
        totalWorks={this.props.workList.length}
        routeKey={getRoute(this.props.match.path)}
      />
    );
    return [
      <MegaMenuContainer key="MegaMenuContainer" path={this.props.match.path} />,
      workFrameView,
      mobileWorkListView,
      workIndicatorView
    ];
  }
}

const mapStateToProps = (state, ownProps) => ({
  workList: selectWorkList(state)
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  loadWork: () => dispatch(loadInteractiveWorkAction())
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(InteractivePageContainer));
