import React from 'react';
import _ from 'lodash';
import classnames from 'classnames';
import Style from './PasswordBox.scss';

const validatePassword = (password, nextRoute, callback) => {
  const input = document.getElementById('inlineFormInputPassword').value;
  if (input === password) {
    if (_.isFunction(callback)) {
      // for WorkDetailPageContainer
      callback();
    } else {
      // default behavior for WorkFrameContainer / MobileWorkListContainer
      document.location.href = `#${nextRoute}`;
    }
  }
};

// https://stackoverflow.com/questions/1563062/prevent-form-submission-with-enter-key
const onKeyDown = e => {
  if (e.keyCode === 13) {
    e.preventDefault();
  }
};

const onKeyUp = (e, ...rest) => {
  // enter
  if (e.keyCode === 13) {
    validatePassword.apply(null, rest);
  }
};

const PasswordBox = ({ password, nextRoute, callback }) => (
  <div className="password-box">
    <form className="form-inline content-box">
      <label className="sr-only" htmlFor="inlineFormInputPassword">
        Password
      </label>
      <input
        type="text"
        className="form-control mb-2 mr-sm-2"
        id="inlineFormInputPassword"
        placeholder="Password"
        onKeyDown={onKeyDown}
        onKeyUp={e => onKeyUp(e, password, nextRoute, callback)}
        autoFocus
      />
      <button
        onClick={e => validatePassword(password, nextRoute, callback)}
        type="button"
        className="btn btn-light mb-2 "
      >
        Enter
      </button>
    </form>
  </div>
);

export default PasswordBox;
