import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import React from 'react';
import { ga } from 'helper/analysisUtil';
import MegaMenuContainer from 'view/component/megamenu/MegaMenuContainer';
import Style from './NotFoundPageContainer.scss';

class NotFoundPageContainer extends React.PureComponent {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    ga.track('/404');
  }

  render() {
    return [
      <MegaMenuContainer key="MegaMenuContainer" path={this.props.match.path} />,
      <section key="not-found-page-container" className="not-found-page-container">
        <h1>404 - Page Not Found!</h1>
        <p>
          <Link to="/home">Take me home</Link>
        </p>
      </section>
    ];
  }
}

const mapStateToProps = (state, ownProps) => ({
  state: state
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  // onClick: () => dispatch(actions())
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(NotFoundPageContainer));
