import React from 'react';
import { HashRouter as Router, Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { NAV } from 'router/config';
import HomePageContainer from 'view/page/HomePageContainer';
import InteractivePageContainer from 'view/page/InteractivePageContainer';
import CreativePageContainer from 'view/page/CreativePageContainer';
import WorkDetailPageContainer from 'view/page/WorkDetailPageContainer';
import AboutMePageContainer from 'view/page/AboutMePageContainer';
import NotFoundPageContainer from 'view/page/NotFoundPageContainer';

// https://is.js.org/#mobile
// console.log(is.mobile());

// doc: https://reacttraining.com/react-router/web/example/basic
const getRouter = defautRoute => (
  <Router>
    <Switch>
      <Route path={`/${NAV.PAGE_HOME.key}`}>
        <HomePageContainer />
      </Route>
      <Route
        exact
        path={`/${NAV.PAGE_INTERACTIVE.key}`}
        render={() => <Redirect to={`/${NAV.PAGE_INTERACTIVE.key}/0`} />}
      />
      <Route exact path={`/${NAV.PAGE_INTERACTIVE.key}/:id`}>
        <InteractivePageContainer />
      </Route>
      <Route exact path={`/${NAV.PAGE_INTERACTIVE.key}/:id/${NAV.PAGE_DETAIL.key}`}>
        <WorkDetailPageContainer />
      </Route>
      <Route exact path={`/${NAV.PAGE_CREATIVE.key}`} render={() => <Redirect to={`/${NAV.PAGE_CREATIVE.key}/0`} />} />
      <Route exact path={`/${NAV.PAGE_CREATIVE.key}/:id`}>
        <CreativePageContainer />
      </Route>
      <Route exact path={`/${NAV.PAGE_CREATIVE.key}/:id/${NAV.PAGE_DETAIL.key}`}>
        <WorkDetailPageContainer />
      </Route>
      <Route exact path={`/${NAV.PAGE_ABOUT.key}`}>
        <AboutMePageContainer />
      </Route>
      <Route exact path="/" render={() => <Redirect to={`/${defautRoute}`} />} />
      <Route>
        <NotFoundPageContainer />
      </Route>
    </Switch>
  </Router>
);

export default getRouter;
