import React from 'react';
import { Link } from 'react-router-dom';
import classnames from 'classnames';
import { isMatching } from 'router/util';
import { NAV } from 'router/config';
import Style from './MegaMenuContainer.scss';
import dribbbleIcon from './icon-dribbble.isvg';
import instagramIcon from './icon-instagram.isvg';
import linkedinIcon from './icon-linkedin.isvg';
import menuToggleIcon from './icon-menu-toggle.isvg';
import menuCloseIcon from './icon-menu-close.isvg';
import megamenuIcon from './icon-megamenu.isvg';

export const MOBILE_HEADER_H = 50;

const OPEN_X = '0px';
const CLOSE_X = '-210px';

class MegaMenuContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    //
    this.megaMenuRef = React.createRef();
  }

  openSideNav() {
    const elMegaMenu = this.megaMenuRef.current;
    elMegaMenu.style.right = OPEN_X;
  }

  closeSideNav() {
    const elMegaMenu = this.megaMenuRef.current;
    elMegaMenu.style.right = CLOSE_X;
  }

  render() {
    const { path } = this.props;
    //
    return [
      <header key="mobile-header" className="mobile-header">
        <h2 />
        <a
          className="toggle-button"
          onClick={e => this.openSideNav()}
          dangerouslySetInnerHTML={{ __html: menuToggleIcon }}
        />
      </header>,
      <aside key="mega-menu-container" ref={this.megaMenuRef} className="mega-menu-container">
        <a
          className="close-menu-button"
          onClick={e => this.closeSideNav()}
          dangerouslySetInnerHTML={{ __html: menuCloseIcon }}
        />
        <a className="mc-logo-button" href="#/" dangerouslySetInnerHTML={{ __html: megamenuIcon }} />
        <nav>
          <ul>
            <li>
              <Link
                className={classnames({ selected: isMatching(path, NAV.PAGE_ABOUT.key) })}
                to={`/${NAV.PAGE_ABOUT.key}`}
              >
                {NAV.PAGE_ABOUT.name}
              </Link>
            </li>
            <li>
              <Link
                className={classnames({ selected: isMatching(path, NAV.PAGE_INTERACTIVE.key) })}
                to={`/${NAV.PAGE_INTERACTIVE.key}`}
              >
                {NAV.PAGE_INTERACTIVE.name}
              </Link>
            </li>
            <li>
              <Link
                className={classnames({ selected: isMatching(path, NAV.PAGE_CREATIVE.key) })}
                to={`/${NAV.PAGE_CREATIVE.key}`}
              >
                {NAV.PAGE_CREATIVE.name}
              </Link>
            </li>
          </ul>
        </nav>
        <ul className="social-media-box">
          <li>
            <a
              href="https://dribbble.com/MengyuCao"
              target="_blank"
              dangerouslySetInnerHTML={{ __html: dribbbleIcon }}
            />
          </li>
          <li>
            <a
              href="https://www.instagram.com/yiqihanzi/"
              target="_blank"
              dangerouslySetInnerHTML={{ __html: instagramIcon }}
            />
          </li>
          <li>
            <a
              href="https://www.linkedin.com/in/caom061/"
              target="_blank"
              dangerouslySetInnerHTML={{ __html: linkedinIcon }}
            />
          </li>
        </ul>
      </aside>
    ];
  }
}

export default MegaMenuContainer;
