import { takeEvery, put, call, fork, select, all } from 'redux-saga/effects';
import { loadInteractiveWorkAction } from 'action/interactiveWorkAction';
import { loadCreativeWorkAction } from 'action/creativeWorkAction';
import { loadAboutMeWorkAction } from 'action/aboutMeAction';
import { REQUEST } from 'action/createAsyncAction';
import { selectWorkList as selectInteractiveWorkList } from 'model/reducer/interactiveWorkReducer';
import { selectWorkList as selectCreativeWorkList } from 'model/reducer/creativeWorkReducer';
import { selectAboutMe } from 'model/reducer/aboutMeReducer';
import { requestInteractiveData, requestCreativeData, requestAboutMeData } from 'model/service/gateway';
import { createWatcher, createAsyncWorker } from 'model/saga/sagaUtil';

function* loadInteractiveWorkData(payload) {
  // const workType = yield select(selectWorkType);
  const { data, error } = yield call(requestInteractiveData);
  if (error) {
    throw error;
  }
  return data;
}

function* loadCreativeWorkData(payload) {
  // const workType = yield select(selectWorkType);
  const { data, error } = yield call(requestCreativeData);
  if (error) {
    throw error;
  }
  return data;
}

function* loadAboutMeData(payload) {
  const { data, error } = yield call(requestAboutMeData);
  if (error) {
    throw error;
  }
  return data;
}

// === should repeat loading data ===
function* shouldProcessRequestInteractiveWork(payload, meta) {
  let shouldRequest = true;
  // only check request
  if (meta.type === REQUEST && meta.persist) {
    const workList = yield select(selectInteractiveWorkList);
    shouldRequest = workList.length === 0;
  }
  return shouldRequest;
}

function* shouldProcessRequestCreativeWork(payload, meta) {
  let shouldRequest = true;
  if (meta.type === REQUEST && meta.persist) {
    const workList = yield select(selectCreativeWorkList);
    shouldRequest = workList.length === 0;
  }
  return shouldRequest;
}

function* shouldProcessRequestAboutMeWork(payload, meta) {
  let shouldRequest = true;
  if (meta.type === REQUEST && meta.persist) {
    const aboutMe = yield select(selectAboutMe);
    shouldRequest = aboutMe === '';
  }
  return shouldRequest;
}

// === all watchers ===
const watchLoadInteractiveWork = createWatcher(
  loadInteractiveWorkAction,
  createAsyncWorker(loadInteractiveWorkAction, loadInteractiveWorkData, shouldProcessRequestInteractiveWork)
);
const watchLoadCreativeWork = createWatcher(
  loadCreativeWorkAction,
  createAsyncWorker(loadCreativeWorkAction, loadCreativeWorkData, shouldProcessRequestCreativeWork)
);
const watchLoadAboutMeWork = createWatcher(
  loadAboutMeWorkAction,
  createAsyncWorker(loadAboutMeWorkAction, loadAboutMeData, shouldProcessRequestAboutMeWork)
);

export default function* root() {
  yield all([fork(watchLoadInteractiveWork), fork(watchLoadCreativeWork), fork(watchLoadAboutMeWork)]);
}
