import React from 'react';
import { subscribeEvent, unsubscribeEvent, RESIZE_TYPE } from 'helper/domEventUtil';
import { createMediaList, removeMediaList, changeVideoSize } from './mediaFactory';
import Style from './MediaListContainer.scss';

export default class MediaListContainer extends React.PureComponent {
  constructor(props) {
    super(props);
    // create a ref to store the textInput DOM element
    this.mediaRef = React.createRef();
  }

  componentDidMount() {
    subscribeEvent(RESIZE_TYPE, this.onWindowResize, this);
  }

  componentWillUnmount() {
    unsubscribeEvent(RESIZE_TYPE, this.onWindowResize);
    removeMediaList();
  }

  onWindowResize() {
    const clientWidth = this.mediaRef.current.clientWidth;
    changeVideoSize(clientWidth, clientWidth);
  }

  onMediaLoad(type) {
    this.onWindowResize();
  }

  render() {
    const media = this.props.media || [];
    return (
      <div ref={this.mediaRef} className="media-list-container">
        {createMediaList(media, type => this.onMediaLoad(type))}
      </div>
    );
  }
}
